import React, { useState, memo } from "react";
import "./style.scss";
import Header from "components/Header/Header";
import Toast from "components/Toast/toast";

const modified = memo(props => {
  let [userInfo, setUserInfo] = useState(() => {
    return JSON.parse(localStorage.getItem("userInfo"));
  });

  let [toastVal,setToastVal]=useState({val:''});
  return (
    <div className="page-wrap">
      <div className="subpage" id="modified">
        <Header title={"修改资料"} {...props} />

        <div className="someThing">
          <div className="aver">
            <span>头像</span>
            <div className="right">
              <img
                src={userInfo.averImg || "https://m.baidu.com/se/static/wiseatom/personalcenter/assets/img/default_icon_02f13d8.png"}
                alt={userInfo.name}
              />
            </div>
          </div>
          <div className="aver">
            <span>昵称</span>
            <div className="right">
              <span>{userInfo.name}</span>
              <i className="iconfont icon-arrow"></i>
            </div>
          </div>
          <div className="aver">
            <span>个人简介</span>
            <div className="right">
              <span>{userInfo.desc || "这个用户好懒呀，啥都没说"}</span>
              <i className="iconfont icon-arrow"></i>
            </div>
          </div>
        </div>

        <div className="login-out" onClick={()=>{
          localStorage.removeItem('ISLOGIN');
          setToastVal({
            val:'退出成功'
          })
          }}>退出当前账号</div>
        <toast val={toastVal}></toast>
      </div>
    </div>
  );
});

export default modified;
